<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                    <h4 name="header" class="modal-title mt-2 pt-1">
                        {{ $t('Edit 3 entries') }}
                    </h4>
                    <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times" @click="$emit('close')"></em> </button>
                </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    defineComponent,
    onMounted,
    reactive,
    ref
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../../stores/TimeTrack.js"
import {
    storeToRefs
} from 'pinia';
import DataService from '../../../services/DataService';

export default defineComponent({

    setup() {
        const store = userTimerInfo();
        const {
            selectedValues
        } = storeToRefs(store)

        return {
            store,
            selectedValues

        }
    },
    props: {
        selectedRecordValues: {
            type: Array,
            required: true
        },
        AssignDpt: {
            type: Array,
            required: true
        }

    },
    data() {
        return {
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "SelectField",
                        label: this.$t('Project'),
                        name: "project_id",
                        value: 0,
                        placeholder: "",
                        config: {
                            options: [],
                            onChange: this.onChangeDropdown
                        },
                        validationRules: {
                            //"required": true
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "SelectField",
                        label: this.$t('Task'),
                        name: "task_id",
                        value: 0,
                        placeholder: "",
                        config: {
                            options: [],
                        },
                        validationRules: {
                            //"required": true
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "CustomSelectField",
                        label: this.$t('Tag'),
                        name: "tag_ids",
                        value: '',
                        mode: "tag",
                        placeholder: "",
                        config: {
                            options: [],

                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "ToggleField",
                        label: this.$t('Billing'),
                        name: "IsProjectBillable",
                        value: false,
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],

                        },
                    }, ],
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "track_description",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            //"required": true
                        },
                        config: {
                            options: [],

                        },
                    }, ],
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            // DataLoaded: 0,
            //TagsSelectedArray: [],
            //departmentList: [],
            //newDepartmentlist: [],
            //ListData: [],
            //TagsSelectedArray1: [],
            //tagsData: [],
            //TagsSelectedArray2: [],
            //TagsSelectedArray3: [],
            // MultiArray: [],
            //obj: ''
        }
    },
    created: function () {
        //this.GetCategoryList();
        this.GetTimerProjectsByUserID();
        //this.LoadTask();
        this.GetCompanyTaglist();
        this.DataLoaded = 1;
        this.pushList();
        this.FetchData(this.AssignDpt);
    },
    methods: {
        closePopup(){
            this.$emit('close')
        },
        async onChangeDropdown(e) {
            this.FormSchema[1].Data[0].config.options = []
            if (e.target.value) {
                this.LoadTask(e.target.value)
            }
        },
        async GetTimerProjectsByUserID() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetTimerProjectsByUserID()
                .then(function (response) {
                    response.data.res.forEach(item => {
                        vm.obj = {
                            name: item.projectName,
                            value: item.projectId
                        }
                        vm.FormSchema[0].Data[0].config.options.push(vm.obj);
                    });

                    vm.isLoading = false;
                });
        },
        async LoadTask(id) {
            var vm = this;
            vm.isLoading = true;
            await DataService.LoadTasks(id)
                .then(function (response) {
                    response.data.forEach(item => {
                        vm.obj = {
                            name: item.text,
                            value: item.value
                        }
                        vm.FormSchema[1].Data[0].config.options.push(vm.obj);
                    });

                    vm.isLoading = false;
                });
        },
        async GetCompanyTaglist() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetCompanyTaglist()
                .then(function (response) {
                    response.data.result.forEach(item => {
                        vm.obj = {
                            name: item.tagName,
                            value: item.tagId
                        }
                        vm.FormSchema[2].Data[0].config.options.push(vm.obj);
                    });

                    vm.isLoading = false;
                });
        },
        async onSubmit(formfields, e) {
            var vm = this;
            var ids = '';
            formfields.tag_ids.forEach(item => {
                ids += item.value + ','
            });
            var trackid = this.store.$state.selectedValues.toString();
            var obj = {
                title: formfields.title,
                project_id: formfields.project_id,
                task_id: formfields.task_id,
                tag_ids: ids.length > 0 ? ids.substring(0, ids.length - 1) : '',
                IsBillable: formfields.IsProjectBillable,
                track_id: trackid,
                track_description: formfields.track_description
            }
            vm.isLoading = true;
            var formData = new FormData();
            formData.append("postString", JSON.stringify(obj));
            await DataService.EditBulkTimeTrackEntry(obj).then(response => {
                vm.isLoading = false;
                if (response.data.status == 0) {
                    vm.ShowAlert(vm.$t('Track Entries is updated'), "success", false, vm.$t("Alert"));
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                    this.store.$state.selectedValues =[]
                    this.store.$state.expendListRow =false
                    this.$emit('close')
                }
                //vm.$parent.FetchData();
               
            })

        }
    }

})
</script>
